<template>
    <div class="checkbox">
        <ul class="test_list_show">
            <li>
                <h3> data   Array  []()</h3>
                <fx-checkbox :data="checkboxList" v-model="checkboxInfo"></fx-checkbox>
                <p>data key: label:  value:</p>
                <p>: {{checkboxInfo}}</p>
            </li>
            <li>
                <h3>data: checked()  false true disable</h3>
                <fx-checkbox :data="checkboxList2"></fx-checkbox>
            </li>
            <li>
                <h3>data: disable()  false true checked</h3>
                <fx-checkbox :data="checkboxList3"></fx-checkbox>
            </li>
            <li>
                <h3>checked  disable </h3>
                <fx-checkbox :data="checkboxList4" v-model="checkboxInfo2"></fx-checkbox>
                <p>: {{checkboxInfo2}}</p>
            </li>
            <li>
                <h3>  </h3>
                <fx-checkbox v-model="checkboxInfo3">
                    <fx-checkboxItem :label="''">
                        <fx-button></fx-button>
                    </fx-checkboxItem>
                    <fx-checkboxItem :label="''" :checked="true">
                        
                    </fx-checkboxItem>
                    <fx-checkboxItem :label="''" :disable="true">
                        <i class="iconfont icon-yes"></i>
                        
                    </fx-checkboxItem>
                    <fx-checkboxItem :label="''">
                        <i class="iconfont icon-close"></i>
                        
                    </fx-checkboxItem>
                    <fx-checkboxItem :label="''" :disable="true" :checked="true">
                        <i class="iconfont icon-close"></i>
                        
                    </fx-checkboxItem>
                </fx-checkbox>
                <p>: {{checkboxInfo3}}</p>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "checkbox",
        data() {
            return {
                checkboxInfo: [],
                checkboxInfo2: [],
                checkboxInfo3: [],
                checkboxList: [
                    {label: '', value: 1},
                    {label: '', value: 2},
                    {label: '', value: 3},
                    {label: '', value: 4},
                    {label: '', value: 5},
                ],
                checkboxList2: [
                    {label: '', value: 1, checked: true},
                    {label: '', value: 2},
                    {label: '', value: 3},
                    {label: '', value: 4, checked: true},
                    {label: '', value: 5},
                ],
                checkboxList3: [
                    {label: '', value: 1},
                    {label: '', value: 2, disable: true},
                    {label: '', value: 3, disable: true},
                    {label: '', value: 4},
                    {label: '', value: 5},
                ],
                checkboxList4: [
                    {label: '', value: 1},
                    {label: '', value: 2, checked: true, disable: true},
                    {label: '', value: 3, disable: true},
                    {label: '', value: 4, checked: true},
                    {label: '', value: 5},
                ],
            }
        },
    }
</script>

<style scoped lang="less">

</style>


