<template>
    <div id="message">
        <ul class="test_list_show">
            <li>
                <h3> </h3>
                <fx-button @clickEvt="messageClick"></fx-button>
                <p>this.$fivexu.message.show({})</p>
            </li>
            <li>
                <h3>  message </h3>
                <fx-button @clickEvt="messageClick2"></fx-button>
                <p>this.$fivexu.message.show({message:''})</p>
            </li>
            <li>
                <h3>  mesType  success warning error</h3>
                <fx-button @clickEvt="messageClick3"></fx-button>
                <p>this.$fivexu.message.show({mesType:'error',message: 'error'})</p>
            </li>
            <li>
                <h3>  transitionName  top button left right</h3>
                <fx-button @clickEvt="messageClick4"></fx-button>
                <p>this.$fivexu.message.show({mesType:'warning',message: 'warning',transitionName:'left'})</p>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "message",
        methods: {
            messageClick() {
                this.$fivexu.message.show({});
            },
            messageClick2() {
                this.$fivexu.message.show({message: ''});
            },
            messageClick3() {
                this.$fivexu.message.show({mesType: 'error', message: 'error'});
            },
            messageClick4() {
                this.$fivexu.message.show({mesType: 'warning', message: 'warning', transitionName: 'left'})
            },
        }
    }
</script>

<style scoped lang="less">

</style>


