<template>
    <div class="radio">
        <ul class="test_list_show">
            <li>
                <h3> data   Array  []()</h3>
                <fx-radio :data="radioList"></fx-radio>
                <p>data key: idid label: </p>
            </li>
            <li>
                <h3>data key: disable(): </h3>
                <fx-radio :data="radioList2" v-model="radioInfo2"></fx-radio>
            </li>
            <li>
                <h3>v-mode: dataid,id</h3>
                <fx-radio :data="radioList" v-model="radioInfo"></fx-radio>
                <p>v-mode: {{radioInfo}}</p>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "radio",
        data() {
            return {
                radioList: [
                    {id: 1, label: ''},
                    {id: 2, label: ''},
                    {id: 3, label: ''},
                    {id: 4, label: ''},
                    {id: 5, label: ''},
                ],
                radioList2: [
                    {id: 1, label: '', disable: true},
                    {id: 2, label: ''},
                    {id: 3, label: '', disable: true},
                    {id: 4, label: ''},
                    {id: 5, label: ''},
                ],
                radioInfo: 3,
                radioInfo2: 1,
            }
        }
    }
</script>

<style scoped lang="less">

</style>


