<template>
    <div id="tooltip">
        <ul class="test_list_show">
            <li>
                <h3>tooltip </h3>
                <fx-button v-tooltip.top="' '"> </fx-button>
                <p>{{tooltip}}</p>
            </li>
            <li>
                <h3>tooltip  top bottom left right</h3>
                <fx-button v-tooltip.top="' '">v-tooltip.top</fx-button>
                <fx-button v-tooltip.bottom="' '">v-tooltip.bottom</fx-button>
                <fx-button v-tooltip.left="' '">v-tooltip.left</fx-button>
                <fx-button v-tooltip.right="' '">v-tooltip.right</fx-button>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "tooltip",
        data() {
            return {
                tooltip: '<fx-button v-tooltip.top="\' \'"> </fx-button>'
            }
        }
    }
</script>

<style scoped lang="less">

</style>


