<template>
    <div id="loading">
        <ul class="test_list_show">
            <li>
                <h3>loading </h3>
                <fx-button @clickEvt="loadingClick"></fx-button>
                <p>this.$fivexu.loading.show()   </p>
                <p>this.$fivexu.loading.hide() </p>
            </li>
            <li>
                <h3>loading show()  text:  textColor:  textPosition:    top;  bottom, bottom</h3>
                <fx-button @clickEvt="loadingClick1">(--.......)</fx-button>
            </li>
            <li>
                <h3>loading show()  bgColor:  : rgba(0, 0, 0, .5)</h3>
                <fx-button @clickEvt="loadingClick2">(rgba(255, 155, 55, .7))</fx-button>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "loading",
        methods: {
            loadingClick() {
                this.$fivexu.loading.show();
                setTimeout(() => {
                    this.$fivexu.loading.hide();
                }, 1000)
            },
            loadingClick1() {
                this.$fivexu.loading.show({
                    text: '......',
                    textPosition: 'top'
                });
                setTimeout(() => {
                    this.$fivexu.loading.hide();
                }, 1000)
            },
            loadingClick2() {
                this.$fivexu.loading.show({
                    bgColor: 'rgba(255, 155, 55, .5)',
                    text: '--#06f',
                    textColor: '#06f'
                });
                setTimeout(() => {
                    this.$fivexu.loading.hide();
                }, 1000)
            },
        }
    }
</script>

<style scoped lang="less">

</style>


