<template>
    <div id="scroll-bar">
        <h3>,,overflow:auto </h3>
        <h3></h3>
        <h3>  barHide  Boolean  false</h3>
        <p>scrollBar,,</p>
        <div class="scroll_bar">
            <fx-scroll-bar>
                <ul>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                </ul>
