            <fx-scroll-bar>
                <ul>
                    <li><img src="../../../static/slider01.jpg"></li>
                </ul>
            </fx-scroll-bar>
        </div>
        <h3> class barClass  String  ''()</h3>
        <p>class,vue stylescoped  !important</p>
        <div class="scroll_bar">
            <fx-scroll-bar :barClass="'bars'">
                <ul>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                    <li>class</li>
                </ul>
            </fx-scroll-bar>
        </div>
    </div>
</template>

<script>
    export default {
        name: "scroll-bar"
    }
</script>

<style lang="less">
    h3, p {
        height: 40px;
        line-height: 40px;
    }

    .scroll_bar {
        display: inline-block;
        width: 150px;
        height: 200px;
        border: 1px solid #ccc;
        margin-bottom: 20px;
    }

    .bars{
        background-color: #f60!important;
    }
</style>


